.message {
    display: flex;
    justify-content: right;
    align-items: center;
    margin-bottom: 15px;
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.imageAndText {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.imageAndText img {
    border-radius: 50%;
    width: 50px;
    height: 50px;
    margin-left: 10px;
    order: 2
}

.text {
    background-color: #007bff;
    color: white;
    border-radius: 10px;
    padding: 10px 20px;
    word-break: break-word;
    text-align: left;
    order: 1
}

.text:after {

}

.name {
    font-size: 14px;
    font-weight: bold;
    text-align: right;
}

.messageText {
    font-size: 16px;
    margin-top: 5px;
    white-space: pre-wrap;
}

.time {
    font-size: 12px;
    color: grey;
    text-align: right;
    margin-left: 10px;

}